* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

ul {
  list-style: none;
}

.search-section {
  background-color: #806fe6;
  padding: 10px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-input {
  width: 350px;
  border: 1px solid #ccc;
  border-right-style: none;
  border-radius: 4px 0 0 4px;
  outline: none;
  padding: 10px;
}

.search-btn {
  cursor: pointer;
  border: 1px solid #ccc;
  border-radius: 0 4px 4px 0;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 5.5px;
  display: flex;
}

